.advantages {
    position: relative;
    max-width: calc(1290px + 2 * $pad15);
    margin: $pad70 auto $pad70;
    padding: 0 $pad15;

    &::after {
        content: '';
        display: block;
        height: 100%;
        width: 100vw;
        background-color: inherit;
        position: absolute;
        top: 0;
        z-index: -1;
        margin: auto;
        left: -50vw;
        right: -50vw;
    }

    .items {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    .item {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: $pad30;

        font-size: $font30;
        text-transform: uppercase;
        color: $white-color;

        .sub-heading {
            font-size: $font14;
            text-transform: capitalize;
        }

        svg {
            position: absolute;
            left: 20px;
            font-size: 40px;
        }
    }

    .item:first-child {
        flex-basis: 30%;

        background-color: $social-media-color;

        svg {
            transform: rotate(-45deg);
        }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: -40px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 55px 0 55px 40px;
            border-color: transparent transparent transparent $social-media-color;
            z-index: 5;
        }
    }
    
    .item:nth-child(2) {
        padding-left: 70px;
        flex-basis: 40%;

        background-color: $black-color; 

        &::before {
            content: '';
            position: absolute;
            top: calc(50% - 100px);
            left: 0;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 100px 0 100px 65px;
            border-color: transparent transparent transparent $white-color;
        }

        &::after {
            content: '';
            position: absolute;
            top: calc(50% - 100px);
            right: 0;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 100px 65px 100px 0;
            border-color: transparent $white-color transparent transparent;
        }

        svg {
            left: 15%;
        }
    }

    .item:last-child {
        flex-basis: 30%;

        background-color: $social-media-color; 

        &::before {
            width: 0;
            content: '';
            position: absolute;
            top: 0;
            left: -40px;
            height: 0;
            border-style: solid;
            border-width: 55px 40px 55px 0;
            border-color: transparent $social-media-color transparent transparent;
            z-index: 5;
        }
    }
}

@media screen and (max-width: 1150px) {
    .advantages {
        .item svg {
            display: none;
        }

        .item {
            align-items: flex-start;
        }
    }
}

@media screen and (max-width: 1024px) {
    .advantages {
        .item:nth-child(2) {
             &::before, &::after {
                content: none;
            }
        }
    }
}

@media screen and (max-width: 970px) {
    .advantages {
        .item:nth-child(1), .item:nth-child(2), .item:nth-child(3) {
            &::before, &::after {
               content: none;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .advantages {
        .items {
            flex-direction: column;
        }

        .item {
            display: flex;
            padding-left: $pad70;
        }

        .item svg {
            display: block;
        }

        .item:nth-child(2) svg {
            left: 20px;
        }

    }
}

@media screen and (max-width: 480px) {
    .advantages {
        .items .item svg {
            display: none;
        }

        .items .item {
            padding: $pad15;

            .heading {
                font-size: $font18;
            }
            
        }
    }
}



