h3 {
    margin: 0;
    padding: 0;
}

.feedback {
    position: relative;
    max-width: calc(1290px + 2 * $pad15);
    margin: 0 auto;
    padding: 0 $pad15;

    figure, figcaption {
        margin: 0;
        padding: 0;
    }

    &::after {
        display: block;
        height: 100%;
        width: 100vw;
        background-color: inherit;
        
        z-index: -1;
        margin: auto;
        left: -50vw;
        right: -50vw;
    }

    .wrapper {
        display: flex;
        justify-content: center;
        align-items: center;

        &::before, &::after {
            content: '';
            position: absolute;
            top: 68px;
            width: 30%;
            height: 1px;
            background-color: $pseudoline-color;
        }

        &::before {
            left: 15px;
        }

        &::after {
            right: 5px;
        }
    }

    .feedback-heading {
        position: relative;

        margin-top: $pad50;
        margin-bottom: $pad50;

        font-family: 'Playfair Display', serif;
        font-style: italic;
        font-weight: bold;
        font-size: $font30;
        text-align: center;

        &::before, &::after {
            content: '';
            position: absolute;
            top: 10px;
            width: 18px;
            height: 18px;
            background-color: $social-media-color;
            transform: rotate(45deg);
        }

        &::before {
            left: -90px;
        }

        &::after {
            right: -90px;
        }
    }

    .feedback-items {
        position: relative;

        display: flex;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        padding: 0 $pad15;

        font-family: 'Playfair Display', serif;
        font-style: italic;

        background-color: $social-media-color;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: calc(50% - 185px);
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 30px 185px 0 185px;
            border-color: $white-color transparent transparent transparent;
        }
    }

    .feedback-item {
        display: flex;
        align-items: flex-end;
        padding-bottom: 90px;


        .feedback-descr {
            display: flex;
            flex-direction: column;
            color: $white-color;
            font-size: $font18;
        }

        p {
            margin-bottom: $pad60;
        }

        .name {
            margin-bottom: $pad15;

            color: $black-color;
            font-weight: bold;
            font-size: $font30;

            text-transform: capitalize;
        }

        .job {
            color: $job-color;
            font-family: 'Lato', sans-serif;
            font-size: $font14;

            text-transform: uppercase;
            font-style: normal;
        }
    }

    .feedback-item:last-child {
        flex-direction: row-reverse;
        padding-left: 100px;

        .paragraph, .feedback-descr {
            text-align: right;
        }
    }
}

@media screen and (max-width: 1024px) {
    .feedback {
        .wrapper::after, .wrapper::before {
            width: 20%;
        } 
        .feedback-items {
            flex-direction: column;
        }
        .feedback-item:last-child {
            padding-left: 0;
        }
    }
}

@media screen and (max-width: 768px) {
    .feedback {
        .wrapper::after, .wrapper::before {
            width: 10%;
        } 
    }
}

@media screen and (max-width: 650px) {
    .feedback {
        .wrapper::after, .wrapper::before {
            content: none;
        } 
    }
}

@media screen and (max-width: 485px) {
    .feedback {
        .feedback-item img {
            display: none;
        }

        .feedback-items::before {
            content: none;
        }
    }
}

@media screen and (max-width: 419px) {
    .feedback {
        .feedback-heading::before {
            left: -30px;
        }
        .feedback-heading::after {
            right: -30px;
        }
    }
}

@media screen and (max-width: 374px) {
    .feedback {
        .feedback-descr p {
            margin-bottom: $pad20;
        }
    }
}