.scroll-button {
    display: none;


    position: fixed;
    bottom: 100px;
    right: 50px;

    width: 40px;
    height: 40px;

    background-color: $black-color;
    border-radius: 5px;

    z-index: 1000;

    &::before {
        content: '';
        position: absolute;
        top: 45%;
        left: 20%;
        width: 40%;
        height: 3px;
        border-radius: 10px;
        background-color: $white-color;
        transform: rotate(-50deg);
    }

    &::after {
        content: '';
        position: absolute;
        top: 45%;
        left: 42%;
        width: 40%;
        height: 3px;
        border-radius: 10px;
        background-color: $white-color;
        transform: rotate(50deg);
    }

    &:hover {
        cursor: pointer;
        background-color: $hover-link-color;
    }
}