.categories {
    position: relative;
    max-width: calc(1290px + 2 * $pad15);
    margin: 0 auto;
    padding: 0 $pad15;

    &::after {
        content: '';
        display: block;
        height: 100%;
        width: 100vw;
        background-color: inherit;
        position: absolute;
        top: 0;
        z-index: -1;
        margin: auto;
        left: -50vw;
        right: -50vw;
    }

    .wrapper {
        display: flex;
        justify-content: center;
        align-items: center;

        &::before, &::after {
            content: '';
            position: absolute;
            top: 43px;
            width: 30%;
            height: 1px;
            background-color: $pseudoline-color;
        }

        &::before {
            left: 15px;
        }

        &::after {
            right: 5px;
        }
    }

    .category-heading {
        position: relative;

        margin-bottom: $pad35;

        font-family: 'Playfair Display', serif;
        font-style: italic;
        font-weight: bold;
        font-size: $font30;
        text-align: center;

        &::before, &::after {
            content: '';
            position: absolute;
            top: 10px;
            width: 18px;
            height: 18px;
            background-color: $social-media-color;
            transform: rotate(45deg);
        }

        &::before {
            left: -90px;
        }

        &::after {
            right: -90px;
        }
    }
}

.categories-items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 20px;
    grid-auto-rows: auto;
    grid-row-gap: 20px;
    grid-template-areas: "first third fourth"
                        "second third fifth";

    
    
    img {
        width: 100%;
        height: 100%;
    }

    .categories-item {
        position: relative;

        outline-offset: -$pad20;
        outline: 3px solid $white-color;

        transition: 0.2s;

        &:hover {
            outline: 3px solid transparent;
        }

        .buy {
            position: absolute;
            bottom: 15%;
            left: calc(50% - 60px);
    
            padding: $pad15 $pad20;
    
            color: $white-color;
            
            text-transform: uppercase;
            background-color: #f0975c;
            z-index: 100;

            &:hover {
                color: #f0975c;
                background-color: $white-color; 
            }

            &:focus, &:active  {
                border-radius: 0;
                box-shadow:  0 0 0 2px #00BFFF;    
            }   
        }   

        .hover-link-item {
            position: absolute;
            top: calc($pad20 - 3px);
            left: calc($pad20 - 3px);
            width: calc(100% - ($pad20 - 3px) * 2);
            height: calc(100% - ($pad20 - 3px) * 2);
            background-color: rgba(0,0,0,.3);
            z-index: 10;
        }

        .hover-link-item::before, .hover-link-item::after {
            content: '';
            position: absolute;
            top: calc(50% - 30px);
            left: calc(50% - 30px);
            width: 60px;
            height: 1px;
            background-color: #f0975c;
            z-index: 50;
        }

        .hover-link-item::after {
            transform: rotate(90deg);
        }
    }

    .first-item {
        grid-area: first;
    }

    .second-item {
        grid-area: second;
    }

    .third-item {
        grid-area: third;
    }

    .fourth-item {
        grid-area: fourth;
        width: 100%;
        height: 100%;
    }

    .fifth-item {
        grid-area: fifth;
    }
}

@media screen and (max-width: 1024px) {
    .categories-items {
        grid-template-columns: repeat(2, 1fr);
        grid-template-areas: "first second"
                            "third fourth "
                            "third fifth ";

        padding: 0 $pad70;
    }

    .categories {
        .wrapper::after, .wrapper::before {
            width: 20%;
        } 
    }
}

@media screen and (max-width: 650px) {
    .categories-items {
        grid-template-areas: "first second"
                            "fifth fourth";

        .third-item {
            display: none;
        }
    }

    .categories {
        .wrapper::after, .wrapper::before {
            content: none;
        } 
    }
}

@media screen and (max-width: 470px) {
    .categories-items {
        padding: 0 0;
    }
}

@media screen and (max-width: 375px) {
    .categories {
        .category-heading::before {
            left: -45px;
        }
        .category-heading::after {
            right: -45px;
        }
        
    }
    
}