.catalog {
    position: relative;
    max-width: calc(1290px + 2 * $pad15);
    margin: $pad70 auto;
    padding: 0 $pad15;

    figure {
        margin: 0;
        padding: 0;
    }

    &::after {
        content: '';
        display: block;
        height: 100%;
        width: 100vw;
        background-color: inherit;
        position: absolute;
        top: 0;
        z-index: -1;
        margin: auto;
        left: -50vw;
        right: -50vw;
    }

    .wrapper {
        display: flex;
        justify-content: center;
        align-items: center;

        &::before, &::after {
            content: '';
            position: absolute;
            top: 43px;
            width: 30%;
            height: 1px;
            background-color: $pseudoline-color;
        }

        &::before {
            left: 15px;
        }

        &::after {
            right: 5px;
        }
    }

    .catalog-heading {
        position: relative;
        margin-bottom: $pad50;

        font-family: 'Playfair Display', serif;
        font-style: italic;
        font-weight: bold;
        font-size: $font30;
        text-align: center;

        &::before, &::after {
            content: '';
            position: absolute;
            top: 10px;
            width: 18px;
            height: 18px;
            background-color: $social-media-color;
            transform: rotate(45deg);
        }

        &::before {
            left: -90px;
        }

        &::after {
            right: -90px;
        }
    }

    .items-wrapper {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 20px;
        grid-auto-rows: auto;
        grid-row-gap: 20px;

        box-sizing: border-box;
        padding: 10px;

        border: 1px solid #eee;
    }

    figure {
        position: relative;
        display: flex;

        .sale {
            position: absolute;
            top: 10px;
            left: 10px;

            display: flex;
            justify-content: center;
            align-items: center;
            width: 60px;
            height: 60px;

            color: $white-color;
            font-size: $font24;

            background-color: $sale-color;
            border-radius: 50%;

            z-index: 10;

            .percent {
                font-size: $font11;
            }
        }

        .add-to-cart {
            position: absolute;
            bottom: 0;
            left: 105px;
            height: 45px;
            padding: $pad15 $pad5 $pad15;

            color: $white-color;

            background-color: $black-color;

            z-index: 10;

            transition: 0.2s;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: -45px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 0 45px 45px;
                border-color: transparent transparent $black-color transparent;
                transition: 0.2s;
            }

            &::after {
                content: '';
                position: absolute;
                top: 0;
                right: -45px;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 45px 0 0 45px;
                border-color: transparent transparent transparent $black-color;
                transition: 0.2s;
            }
        }

        .add-to-cart:focus {
            border-radius: 0;
            outline: none;
            background-color: $hover-link-color;
        }

        img {
            transition: 0.2s;
        }

         &:hover img {
            filter: brightness(40%);
        } 

        &:hover .add-to-cart {
            background-color: $hover-link-color;
        }

        &:hover .add-to-cart::before {
            border-color: transparent transparent $hover-link-color transparent;
        }
        &:hover .add-to-cart::after {
            border-color: transparent transparent transparent $hover-link-color;
        }
    }
}

@media screen and (max-width: 1280px) {
    .catalog {
        .items-wrapper {
            grid-template-columns: repeat(2, 1fr);
        }

        figure {
            margin: 0 auto;
        }
    }
}

@media screen and (max-width: 1180px) {
    .catalog {
        .wrapper::after, .wrapper::before {
            width: 20%;
        } 
    }
}

@media screen and (max-width: 820px) {
    .catalog {
        .wrapper::after, .wrapper::before {
            width: 10%;
        } 
    }
}

@media screen and (max-width: 820px) {
    .catalog {
        .items-wrapper {
            grid-template-columns: repeat(1, 1fr);
        }

        figure {
            margin: 0 auto;
        }
    }
}

@media screen and (max-width: 650px) {
    .catalog {
        .wrapper::after, .wrapper::before {
            content: none;
        } 
    }
}

@media screen and (max-width: 470px) {
    .catalog {
        .catalog-heading::before {
            left: -45px;
        }
        .catalog-heading::after {
            right: -45px;
        }
        
    }
}

@media screen and (max-width: 375px) {
    .catalog {
        .catalog-heading::before {
            left: -23px;
        }
        .catalog-heading::after {
            right: -23px;
        }
    }
}