.slider-item {
    position: relative;

    font-weight: 700;
    text-transform: uppercase;

    text-align: center;
    
    .slide-image {
        width: 100%;
        height: auto;
        display: flex;
    }

    .slide-info {
        position: absolute;
        
        top: 18%;
        
        left: calc(50% - $slide-item-info-width / 2);

        box-sizing: border-box;
        padding: $pad5;

        font-weight: 700;
        text-transform: uppercase;

        border: 2px solid $white-color;

        .sub-heading:first-child {
            padding: $pad30;

            color: $white-color;
            font-size: $font20;
        }

        span:nth-child(2) {
            padding-bottom: $pad20;

            color: $black-color;
            font-size: $font60;
        }

        div.decoration {
            position: relative;

            padding-bottom: $pad20;

            &::before, &::after {
                content: '';
                position: absolute;
                left: 70%;
                height: 1px;
                background-color: $white-color;
            }

            &::before {
                top: 15%;
                width: $pad35;
            }

            &::after {
                top: 38%;
                width: $pad60;
            }
        }

        div.decoration span {
            position: relative;

            color: $white-color;
            font-size: $font20;
            
            &::before, &::after {
                content: '';
                position: absolute;
                right: 140%;
                height: 1px;
                background-color: $white-color;
            }

            &::before {
                top: 15%;
                width: $pad35;
            }

            &::after {
                top: 30%;
                width: $pad60;
            } 
        }

        span:last-child {
            padding-bottom: $pad35;

            color: $black-color;
            font-size: $font32;
        }
    }

    .slide-info-wrapper {
        display: flex;
        flex-direction: column;
        min-width: $slide-item-info-width;

        border: 1px solid $white-color;
    }

    .shop-now {
        position: absolute;
        top: 110%;
        left: calc(50% - $slide-button-width / 2);

        padding: $pad15 $pad30;
        min-width: $slide-button-width;

        color: $white-color;
        font-size: $font24;

        background-color: $hover-link-color;

        &:hover {
            color: $hover-link-color;

            background-color: $white-color;
        }

        &:focus, &:active {
            color: $hover-link-color;
            
            background-color: $white-color;
            border-radius: 0;
        }
    }
}