.main-header {
    max-width: calc(1290px + 2 * $pad15);
    margin: 0 auto;
    padding: 0 $pad15;
}

.top-menu {
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    font-size: $font11;
    color: $text-color;

    background-color: $header-bg;

    a {
        display: flex;
        align-items: center;

        &:hover {
            color: $hover-link-color;
        }

        &:focus {
            box-shadow: 0 0 0 4px $header-bg, 0 0 0 6px #00BFFF;
        }
    }

    svg {
        margin-right: $pad20;
    }

    &::after {
        content: '';
        display: block;
        height: 100%;
        width: 100vw;
        background-color: inherit;
        position: absolute;
        top: 0;
        z-index: -1;
        margin: auto;
        left: -50vw;
        right: -50vw;
    }
} 

.top-menu-list {
    display: flex;
    justify-content: space-around;
    padding: 0;
    padding-right: calc($pad10 + $pad20);

    list-style-type: none;

    li {
        padding-left: calc($pad20 + $pad5);
        
        text-transform: uppercase;
    }

    li:not(:last-child) {
        padding-right: calc($pad20 + $pad5);
        border-right: 1px solid $decoration-el;
    }
}

@media screen and (max-width: 720px) {
    .top-menu {
        flex-direction: column;
    }

    .top-menu a {
        margin-top: $pad10;
        margin-bottom: $pad10;
    }
    .top-menu-list {
        padding-right: 0;
    }

    .top-menu-list a {
        padding-top: $pad10 / 2;
        padding-bottom: $pad10 / 2;
    }

    .top-menu-list li:first-child {
        padding-left: 0;
    }
}

@media screen and (max-width: 480px) {
    .top-menu-list {
        display: none;
    }
}




.main-menu{
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: $pad35 0;

    &::after {
        z-index: 10;
        content: '';
        position: absolute;
        bottom: calc(0% - 65px);
        left: calc(50% - $header-triangle-width);
        width: 0;
        height: 0;

        border-style: solid;
        border-width: 65px $header-triangle-width 0 $header-triangle-width;
        border-color: #fff transparent transparent transparent;
    }
}

.header-nav ul {
    display: flex;
    list-style-type: none;
    padding: 0;

    font-family: 'Playfair Display', serif;
    font-style: italic;
    font-size: $font18;

    li {
        padding-right: $pad20;

        list-style-type: none;
    }
    
    a:hover {
        color: $hover-link-color;
    }

    a:focus {
        box-shadow: 0 0 0 4px $header-bg, 0 0 0 6px #00BFFF;
    }
}

figure.logo {
    position: absolute;
    top: 25%;
    left: calc(50% - 228px / 2);

    margin: 0;
    padding: 0;
}


.social-media {
    display: flex;  
    justify-content: space-between;
    align-items: center;

    margin: 0 $pad35 0 auto;
    padding: 0;

    li {
        padding-right: $pad10; 

        list-style-type: none;

        a:hover {
            border-color: $black-color;
        }

        a:hover svg {
            fill: $black-color;
            border-color: $black-color;
        }

        a:focus {
            border-color: $black-color;
        }

        a:focus svg {
            fill: black;
            border-color: $black-color;
        }
    }

    svg {
        padding: 7px;

        font-size: 35px;

        fill: $social-media-color;
        border: 2px solid $social-media-color;
        border-radius: 100%;
    }
}

.currency {
    padding: $pad10 $pad20 $pad10 $pad15;

    font-size: $font12;
    text-transform: uppercase;

    border: none;
    color: $white-color;
    background-color: $black-color;

    &:focus {
        outline: none;
        box-shadow: 0 0 0 4px $white-color, 0 0 0 6px #00BFFF;
        border-radius: none;
    }
}

@media screen and (max-width: 1024px) {
    .main-menu {
        flex-direction: column;
        padding: 0;

        background-position: 50% 30%;
    }

    .header-nav {
        /* padding-bottom: $pad20; */
    }

    figure.logo {
        position: initial;

        padding-bottom: $pad20;
    }

    .social-media {
        margin: 0;
        padding-bottom: $pad20;
    }
}

@media screen and (max-width: 480px) {
    figure.logo {
        text-align: center;
    }

    figure.logo img {
        width: 50%;
    }

    .main-menu::after {
        $header-triangle-width: 100px;

        bottom: calc(0% - $pad50);
        left: calc(50% - $header-triangle-width);
        border-width: $pad50 $header-triangle-width 0 $header-triangle-width;
    }
    .header-nav li:last-child {
        padding: 0;
    }
}


