.main-footer {
    position: relative;
    max-width: calc(1290px + 2 * $pad15);
    margin: 0 auto;
    padding: 0  $pad15 $pad70;

    color: $footer-text-color;
    text-transform: capitalize;
    font-size: $font14;

    background-color: $footer-color;

    ul, li {
        margin: 0;
        padding: 0;
        list-style-type: none;
    }

    h3 {
        margin-top: $pad70;
        margin-bottom: $pad50;
        color: $white-color;
        text-transform: uppercase;
        font-size: $font16;
        font-weight: bold;
    }

    a:hover {
        color: $white-color;
        text-decoration: underline;
    }

    a:focus {
        box-shadow: 0 0 0 4px $footer-color, 0 0 0 6px #00BFFF;
    }

    &::after {
        content: '';
        display: block;
        height: 100%;
        width: 100vw;
        background-color: inherit;
        position: absolute;
        top: 0;
        z-index: -1;
        margin: auto;
        left: -50vw;
        right: -50vw;
    }

    .footer-nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: $pad70;
    }

    .footer-shops li:not(:last-child),
    .footer-info li:not(:last-child),
    .footer-service li:not(:last-child) {
        padding-bottom: $pad15;
    }

    .social {
        padding-right: 90px;
    }

    .footer-social {
        display: flex;
        justify-content: space-around;
        align-items: center;

        li {
            display: flex;
            justify-content: center;
            align-items: center;
        }

        li:not(:first-child) {
            margin-left: $pad10;
        }
 
        a {
            padding: $pad15;
            border-radius: 50%;
            
            background-color: $white-color;
        }

        a:hover, a:focus {
            
            background-color: $footer-color;
        }

        a:hover svg, a:focus svg {
            fill: $white-color;
        }

        svg {
            fill: $black-color;
            font-size: 25px;
        }
        
    }

    .subscribe-form {
        position: relative;

        margin-top: $pad30;

        fieldset {
            border: none;
        }

        legend {
            padding-bottom: $pad50;

            color: $white-color;

            text-transform: uppercase;
        }

        .custom {
            font-family: 'Playfair Display', serif;
            text-transform: lowercase;
            font-style: italic;
        }

        .form-wrapper {
            position: relative;
        }

        input {
            padding: $pad15 120px $pad15 $pad30;
            width: 365px;

            border: 1px solid $form-border-color;
            background-color: inherit;

            &:focus {
                outline: none;
                box-shadow: 0 0 0 4px $footer-color, 0 0 0 6px #00BFFF;
            }
        }

        button {
            position: absolute;
            top: calc(50% - 20px);
            right: 4px;
            height: 40px;
            width: 100px;
            background-color: $form-button-color;
            border: none;
            color: $white-color;
            text-transform: uppercase;

            &:hover {
                cursor: pointer;
                color: $black-color;
                background-color: $white-color;
            }

            &:focus {
                outline: none;
                box-shadow: 0 0 0 4px $footer-color, 0 0 0 6px #00BFFF;
            }
        }
    }

    .copyright {
        display: block;
        
        font-size: $font11;
        line-height: 20px;

        border-top: 1px solid $form-border-color;
        text-transform: capitalize;
    }

    &::before {
        content: '';
        position: absolute;
        bottom: 182px;
        left: calc(50% - 13px);
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 10px 13px 0 13px;
        border-color: $form-border-color transparent transparent transparent;
    }

    .pay-system svg {
        margin-right: $pad30;
        font-size: 40px;
    }
    
}

.fill {
    position: relative;
    padding: $pad35;
    background-color: $black-color;

    &::after {
        content: '';
        display: block;
        height: 100%;
        width: 100vw;
        background-color: inherit;
        position: absolute;
        top: 0;
        z-index: -1;
        margin: auto;
        left: -50vw;
        right: -50vw;
    }
}

@media screen and (max-width: 480px) {
    .main-footer {
        padding-bottom: $pad10; 

        .footer-shops, .footer-info, .footer-service {
            display: none;
        }

        &::before {
            bottom: 122px;
        }

        .footer-social {
            li:not(:first-child) {
                margin-left: $pad5;
            }
     
            a {
                padding: $pad5;
                border-radius: 50%;
                background-color: $white-color;
            }
    
            svg {
                fill: $black-color;
                font-size: 18px;
            }
            
        }

        .social form input {
            width: 280px;
        }

        h3 {
            margin: $pad20 0;
        }

        .subscribe-form fieldset {
            padding: $pad10 0;
        }

        .subscribe-form legend {
            padding-bottom: $pad15;
        }

        .footer-nav {
            margin: 0;
        }
    }
}

@media screen and (max-width: 374px) {
    .main-footer {

        
    }
    
} 